        this.TimeDown(this.endtime)
      }

    },
    TimeDown(overtime) {
      //结束时间
      var overtime = overtime
      var endDate = new Date(new Date(overtime).getTime()+24*60*60*1000-1);
      //当前时间
      var nowDate = new Date();
      //相差的总秒数
      var totalSeconds = parseInt((endDate - nowDate) / 1000);
      //天数
      var days = Math.floor(totalSeconds / (60 * 60 * 24));
      //取模（余数）
      var modulo = totalSeconds % (60 * 60 * 24);
      //小时数
      var hours =this.PrefixInteger(Math.floor(modulo / (60 * 60)),2);
      modulo = modulo % (60 * 60);
      //分钟
      var minutes = this.PrefixInteger(Math.floor(modulo / 60),2);
      //秒
      var seconds = this.PrefixInteger(modulo % 60,2);
      //输出到页面
      this.overTime =  hours + ":" + minutes + ":" + seconds ;
      //延迟一秒执行自己
      setTimeout(()=>{
        this.TimeDown(overtime);
      }, 1000)
    },
    allTimeDown(endtime){
      //结束时间
      var endDate = new Date(new Date(this.endtime).getTime()+24*60*60*1000-1);
      //当前时间
      var nowDate = new Date();
      //相差的总秒数
      var totalSeconds = parseInt((endDate - nowDate) / 1000);
      //天数
      var days = Math.floor(totalSeconds / (60 * 60 * 24));
      //取模（余数）
      var modulo = totalSeconds % (60 * 60 * 24);
      //小时数
      var hours =this.PrefixInteger(days*24+Math.floor(modulo / (60 * 60)),2);
      modulo = modulo % (60 * 60);
      //分钟
      var minutes = this.PrefixInteger(Math.floor(modulo / 60),2);
      //秒
      var seconds = this.PrefixInteger(modulo % 60,2);
      //输出到页面
      this.overTimeAll =  hours + ":" + minutes + ":" + seconds ;
      //延迟一秒执行自己
      setTimeout(()=>{
        this.allTimeDown(this.endtime);
      }, 1000)
    },
