    hitCharts(coinname,day){
      var coinname = coinname.toLowerCase();
      var day = day || ''
      var data = {
        name:'-',
        time:'-',
        ph:'-',
        wins:'-',
        reward:'-'
      }
      api.getTableRows({
        "code": "bitpiedaisiu",
        "table": "scores",
        "json": "true",
        "key_type": "i64",
        "index_position": "2",
        "limit": 20,
        scope: coinname + day
      }).then((results) => {
        if(results.rows){
          this.hitChartsData = results.rows
          if(this.account.name){
            data.name = this.account.name
            api.getTableRows({
              "scope": coinname + day,
              "code": "bitpiedaisiu",
              "table": "scores",
              "json": "true",
              "key_type": "i64",
              "index_position": "1",
              "lower_bound":this.account.name,
              "limit": 1
            }).then((results) => {
              if(results.rows&&results.rows[0]&&results.rows[0].player==this.account.name){
                console.log(results)
                data.time=results.rows[0].win_at;
                data.wins = results.rows[0].wins;
                for(var i=0;i<this.hitChartsData.length;i++){
                  if(this.hitChartsData[i].player === this.account.name){
                    data.ph = i+1
                  }
                }
                if(results.rows[0].last_won ===1){
                  this.lastWins = results.rows[0].last_count
                }else{
                  this.lastWins =0
                }
              }else{
                this.lastWins =0
              }
              this.ownWin = data
            })
          }
        }
      })
    },
    getWins(coinname){
      var coinname = coinname.toLowerCase();
      api.getTableRows({
        "scope": coinname,
        "code": "bitpiedaisiu",
        "table": "scores",
        "json": "true",
        "key_type": "i64",
        "index_position": "1",
        "lower_bound":this.account.name,
        "limit": 1
      }).then((results) => {
        if(results.rows){
          var wins = results.rows[0].last_count
          this.lastWins = wins
          var that = this
          this.hitCharts(this.hbBtn.toLowerCase(),this.phBtn == 1?'': this.thisday)
          if(wins<2){
            this.winOpen= true
          }else if(wins>=2){
            this.hit(wins,'80')
          }
        }
      })
    },
    changeDate(datetimes){
      var date = new Date(datetimes/1000);
      if(date&&date.getHours()){
        var Y = date.getFullYear() + '-';
        var M = (date.getMonth()+1 < 10 ? '0'+(date.getMonth()+1) : date.getMonth()+1) + '-';
        var D = date.getDate() + ' ';
        var h = this.PrefixInteger(date.getHours(),2) + ':';
        var m = this.PrefixInteger(date.getMinutes(),2) + ':';
        var s = this.PrefixInteger(date.getSeconds(),2);
//      年 月 日 时 分 秒
        return h+m+s;
      }else{
        return '--:--:--'
      }

    },
    nameCut(str){
      var str = str
      if(str.length<=6){
        return str
      }else{
        return str.substring(2,0) + '**'+ str.substring(str.length,str.length-2)
      }
