      const { animating } = this;
      if (!animating) {
        clearInterval(this.timer);
        this.actionTxt =this.$t('start')
        this.actionnum = false
        return;
      }
      this.timer = setInterval(() => {
        this.actionnum =(Math.random() * 100).toFixed(0)
      }, 100);
    },
    lastWins:function (curVal,oldVal){
      this.pagehit(curVal,'50')
    }
  },
  components: {
    vueSlider
  },
  computed: {
    referralsText () {
      if (this.$store.state.account.name) {
        return 'https://dice.betsand.com/?ref=' + this.$store.state.account.name
      }
      return 'https://dice.betsand.com/?ref='
    },
    winChance () {
      return this.sliderModel.value - 1;
    },
    payOut () {
      return 98.5 / this.winChance;
    },
    payWin () {
      return (this.eos * this.payOut).toFixed(4);
    },
    account () {
      return this.$store.state.account;
    },
    betAmount () {
      return this.$t('bet_amount')
    },
    payoutWin () {
      return this.$t('pay_out_win')
    },
    rollUnderWin () {
      return this.$t('roll_under_win')
    },
    rollUnderWinDa () {
      return this.$t('roll_under_win_da')
    },
    payoutLan () {
      return this.$t('payout')
    },
    winChanceLan () {
      return this.$t('win_chance')
    },
    loginLan () {
      return this.$t('login')
    },
    rollUnderWinSmall () {
      return this.$t('rool_under_win_small')
    },
    rollUnderWinSmallDa () {
      return this.$t('rool_under_win_small_da')
    },
    howPlay () {
      return this.$t('how_to_play')
    },
    rewardRefferals () {
      return this.$t('reward_referrals')
    },
    copy () {
      return this.$t('copy')
    },
    fairness () {
      return this.$t('fairness')
    },
    seedPlaceholder () {
      return this.$t('seed_placeholder')
    },
    update () {
      return this.$t('update')
    },
    joinCommuntity () {
      return this.$t('join_communtity')
    },
    dayTitle(){
      return this.$t('day_title')
    },
    allTitle(){
      return this.$t('all_title')
    },
    phtitle() {
      return this.$t('phtitle')
    },
    actionTxt: {
      get(){
        return this.$t('start')
      },
      set(){
        return this.$t('start')
      }
    },
    big(){
      return this.$t('big')
    },
    small(){
      return this.$t('small')
    },
    lang(){
      return this.$store.state.lang
    },
    phTitleName(){
      return this.$t('phTitleName')
    },
    hitWinsFont(){
      return this.$t('winintro', { amount: this.lastWins })
    },
    notstart(){
      return this.$t('notstart')
    },
    EOSnotstart(){
      return this.$t('EOSnotstart')
    },
    prompt(){
      return this.$t('prompt')
    }
  }
};
