<template>
  <div class="order-box">
    <section class="orders-container">
      <header class="orders-tab">
        <el-row class="order-title-box">
          <el-col class="order-title" :span="12" :class="{'on':orderType===1}" @click.native="orderClick(1)">{{allBet}}</el-col>
          <el-col class="order-title" :span="12" :class="{'on':orderType===2}" @click.native="orderClick(2)">{{myBet}}</el-col>
        </el-row>
      </header>
      <el-row class="orders-table">
          <el-col class="order-title-font" :span="8">{{bettor}}</el-col>
          <el-col class="order-title-font" :span="3">{{roll_under}}</el-col>
          <el-col class="order-title-font" :span="3">{{roll}}</el-col>
          <el-col class="order-title-font" :span="10">{{payout}}</el-col>
      </el-row>
      <div class="tbody-box">
        <div class="orders-table">
          <el-row :key="index" class="selfrow" v-for="(order, index) in orders" :class="order.prize !== '0.0000 EOS' ? 'success':'error'">
            <img v-if="order.player===account.name&&orderType==1" src="../assets/selficon.png" class="selficon" alt="">
            <!--<el-col :span="12" class="hidden-md-and-down">{{dateFormat(order.created_at)}}</el-col>-->
            <el-col :span="8">{{order.player}}</el-col>
            <el-col :span="3">{{order.big_small==0?small:big}}</el-col>
            <el-col :span="3">{{order.result}}</el-col>
            <!--<el-col :span="12" class="hidden-500">{{order.amount}}</el-col>-->
            <el-col :span="10" class="payout">
              {{priceCut(order.prize)}}
            </el-col>
          </el-row>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
import api from '@/utils/eosOrder';
import config from '@/utils/config';

export default {
  mounted () {
    this.fetchOrders()
    this.interval = setInterval(this.fetchOrders, 10000);
    let that = this
    document.addEventListener('visibilitychange', ()=>{
      if (document.hidden) {
        clearInterval(this.interval)
      } else {
        this.interval = setInterval(this.fetchOrders, 10000);
      }

    })

  },

  data () {
    return {
      orders: [],
      orderType:1,
      interval:null
    };
  },

  methods: {
    fetchOrders () {
      if(this.orderType===1){
        api.getTableRows({
          "scope": "bitpiedaisiu",
          "code": "bitpiedaisiu",
          "table": "results",
          "json": "true",
          "limit": 40
        }).then((results) => {
          if(results&&results.rows){
            this.orders = results.rows
          }
        })
      }else if(this.orderType===2){
        var data = []
        if(this.account&&this.account.name){
          api.getTableRows({
            "scope": "bitpiedaisiu",
            "code": "bitpiedaisiu",
            "table": "results",
            "json": "true",
            "key_type": "i64",
            "index_position": "2",
            "lower_bound":this.account.name,
            "limit": 40
          }).then((results) => {
            if(results&&results.rows){
              for(var i=0;i<results.rows.length;i++){
                if(results.rows[i].player===this.account.name){
                  data.push(results.rows[i])
                }
              }
              this.orders = data
            }
          })
        }else{
          this.orders = []
        }

      }

    },
    PrefixInteger(num, n) {
      return (Array(n).join(0) + num).slice(-n);
    },
    dateFormat (datetimes){
      var date = new Date(datetimes/1000);
      if(date&&date.getHours()){
        var Y = date.getFullYear() + '-';
        var M = (date.getMonth()+1 < 10 ? '0'+(date.getMonth()+1) : date.getMonth()+1) + '-';
        var D = date.getDate() + ' ';
        var h = this.PrefixInteger(date.getHours(),2) + ':';
        var m = this.PrefixInteger(date.getMinutes(),2) + ':';
        var s = this.PrefixInteger(date.getSeconds(),2);
//      年 月 日 时 分 秒
        return h+m+s;
      }else{
        return '--:--:--'
      }

    },
    orderClick(num){
      if(num === 1){
        this.orderType = num
        this.fetchOrders()
      }else if(num === 2 && this.account){
        this.orderType = num
        this.fetchOrders()
      }else{
        this.orderType = 1
      }

    },
    priceCut(str){
      var str = str.split(" ");
      if(parseFloat(str[0])===0){
        return ''
      }
      return parseFloat(str[0]) +' ' + str[1]
    }
  },

  computed: {
    allBet () {
      return this.$t('all_bet');
    },
    myBet () {
      return this.$t('my_bet');
    },
    time () {
      return this.$t('time');
    },
    bettor () {
      return this.$t('bettor');
    },
    roll_under () {
      return this.$t('roll_under');
    },
    bet () {
      return this.$t('bet');
    },
    roll () {
      return this.$t('roll');
    },
    payout () {
      return this.$t('list_payout');
    },
    account() {
      return this.$store.state.account
    },
    big(){
      return this.$t('lbig')
    },
    small(){
      return this.$t('lsmall')
    }
  }
};
</script>

